const express = require('express');
const mongoose = require('mongoose');
const multer = require('multer');
const { users } = require('../models/Pdashmodouls');

const router = express.Router();

// Configure multer for file uploads
const storage = multer.diskStorage({
  destination: (req, file, cb) => {
    cb(null, 'uploads/'); // Ensure this folder exists
  },
  filename: (req, file, cb) => {
    cb(null, Date.now() + '-' + file.originalname);
  },
});
const upload = multer({ storage });

// Existing /notify route (unchanged)
router.get('/notify', async (request, res) => {
  try {
    console.log("fetched");
    res.status(200).json({ message: "Fetched successfully" });
  } catch (error) {
    console.log(error, "fetching error");
    res.status(500).json({ error: "Failed to fetch data" });
  }
});

// Updated /submit-checkpoint route to use insertOne
router.post('/submit-checkpoint', upload.single('photo'), async (req, res) => {
  try {
    const { vehicleNumber, latitude, longitude } = req.body;
    const photoPath = req.file ? req.file.path : null;

    if (!vehicleNumber) {
      return res.status(400).json({ error: 'Vehicle number is required' });
    }

    // Create a new document to insert into the users collection
    const newCheckpoint = {
      vehicleNumber,
      photoPath,
      latitude: latitude || null,
      longitude: longitude || null,
      createdAt: new Date(), // Add timestamp for when the document is created
    };

    // Insert the new document using insertOne
    await users.insertOne(newCheckpoint);

    res.status(200).json({ message: 'Checkpoint data submitted successfully' });
  } catch (error) {
    console.error('Error submitting checkpoint data:', error);
    res.status(500).json({ error: 'Failed to submit checkpoint data' });
  }
});

module.exports = router;